"use client"

import { routes } from "@/lib/routes"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import Link from "next/link"
import { useState } from "react"

export default function DoctorDetailsPage({ params }: { params: { id: string } }) {
  const doctor = {
    id: params.id,
    name: "Dr. Sarah Johnson",
    specialty: "Cardiologist",
    experience: 12,
    rating: 4.8,
    qualifications: ["MBBS", "MD - Cardiology", "Fellowship in Interventional Cardiology"],
    bio: "Dr. Sarah Johnson is a highly experienced cardiologist with over 12 years of practice in the field. She specializes in preventive cardiology, coronary artery disease, and heart failure management.",
    clinics: [
      { name: "City Heart Clinic", location: "123 Main St, Downtown" },
      { name: "Medical Center", location: "456 Oak Ave, Midtown" },
    ],
    availableSlots: ["09:00 AM", "10:30 AM", "02:00 PM", "03:30 PM"],
  }

  const [selectedSlot, setSelectedSlot] = useState<string>("")

  return (
    <main className="min-h-screen bg-background">
      <div className="max-w-4xl mx-auto px-4 py-12">
        {/* Doctor Info Card */}
        <Card className="mb-8">
          <CardHeader className="flex flex-row items-start justify-between">
            <div className="space-y-2">
              <CardTitle className="text-4xl">{doctor.name}</CardTitle>
              <CardDescription className="text-lg">{doctor.specialty}</CardDescription>
            </div>
            <div className="text-right">
              <div className="text-3xl font-bold">⭐ {doctor.rating}</div>
            </div>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="grid md:grid-cols-2 gap-4">
              <div>
                <h3 className="font-semibold mb-2">Experience</h3>
                <p className="text-muted-foreground">{doctor.experience} years in practice</p>
              </div>
              <div>
                <h3 className="font-semibold mb-2">Qualifications</h3>
                <ul className="text-muted-foreground space-y-1">
                  {doctor.qualifications.map((qual) => (
                    <li key={qual}>• {qual}</li>
                  ))}
                </ul>
              </div>
            </div>
          </CardContent>
        </Card>

        {/* About Section */}
        <Card className="mb-8">
          <CardHeader>
            <CardTitle>About</CardTitle>
          </CardHeader>
          <CardContent>
            <p className="text-muted-foreground leading-relaxed">{doctor.bio}</p>
          </CardContent>
        </Card>

        {/* Clinics Section */}
        <Card className="mb-8">
          <CardHeader>
            <CardTitle>Practice Locations</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              {doctor.clinics.map((clinic, index) => (
                <div key={index} className="flex items-center gap-4 p-4 border border-border rounded-lg">
                  <div>
                    <p className="font-semibold">{clinic.name}</p>
                    <p className="text-sm text-muted-foreground">{clinic.location}</p>
                  </div>
                </div>
              ))}
            </div>
          </CardContent>
        </Card>

        {/* Book Appointment Section */}
        <Card>
          <CardHeader>
            <CardTitle>Book an Appointment</CardTitle>
          </CardHeader>
          <CardContent className="space-y-4">
            <div>
              <h3 className="font-semibold mb-3">Available Time Slots</h3>
              <div className="grid grid-cols-2 md:grid-cols-4 gap-3">
                {doctor.availableSlots.map((slot) => (
                  <Button
                    key={slot}
                    variant={selectedSlot === slot ? "default" : "outline"}
                    className={`w-full bg-transparent ${
                      selectedSlot === slot
                        ? "bg-blue-500 text-white ring-2 ring-blue-400"
                        : ""
                    }`}
                    onClick={() => setSelectedSlot(slot)}
                  >
                    {slot}
                  </Button>
                ))}
              </div>
            </div>
            <Button size="lg" className="w-full">
              Confirm Booking
            </Button>
            <Button
              size="lg"
              variant="outline"
              className="w-full mt-2"
              onClick={() => {
                const a = document.createElement("a");
                a.href = "/appointment_receipt.pdf";
                a.download = "appointment_receipt.pdf";
                a.click();
              }}
              disabled={!selectedSlot}
            >
              Receipt Download
            </Button>
          </CardContent>
        </Card>

        <div className="mt-8">
          <Link href={routes.doctors.list}>
            <Button variant="outline">← Back to Doctors</Button>
          </Link>
        </div>
      </div>
    </main>
  )
}
